export default {
	data() {
		return {
				tabNum:1,//1-稳态报表 2-暂降报表
				steadyDateChoose:0,
				transientDateChoose:0,
				userInfo:{},
				steadyDate:'周报',
				steadyPage:1,
				steadyLine:{lineName:'请选择'},
				transientPage:1,
				transientDate:'月报',
				// steadyList:[{"name":1,"listResult":{"result":"暂无数据"}},{"name":1}],
				steadyList:[],
				transientList:[],
				transientLine:{lineName:'请选择'},
				types:['年报','季报'],
				typesIndex:0,
				years:[2010,2011,2012,2013,2014,2015,2016,2017,2018,2019,2020,2021,2022,2023,2024,2025,2026,2027,2028,2029,2030],
				yearsIndex:9,
				yearsAndQuarter:[[2010,2011,2012,2013,2014,2015,2016,2017,2018,2019,2020,2021,2022,2023,2024,2025,2026,2027,2028,2029,2030],['第一季度','第二季度','第三季度','第四季度']],
				yearsAndQuarterIndex1:9,
				yearsAndQuarterIndex2:0,
				scrollHeight:'',
				swiperHeight:'',
				loadingFlag:false,
				disabledFlag:false,
				current:0,
				scrollFlag:true
		}
	},
	methods: {
		scrollH(){
			var that =this;
			var sys = uni.getSystemInfoSync();
			var winWidth = sys.windowWidth;
			var winrate = 750/winWidth;
			var winHeight= sys.windowHeight;
			var statusBarHeight = sys.statusBarHeight;
			that.scrollHeight = parseInt((winHeight-statusBarHeight-50)*winrate-274)+'rpx';
			that.swiperHeight=parseInt((winHeight-statusBarHeight-50)*winrate-172)+'rpx';
		},
		setScrollFlag(){
			var that = this;
			that.scrollFlag=false;
			setTimeout(function(){
				that.scrollFlag=true;
			},30)
		},
		changeTab(tabNum){
			this.tabNum=tabNum;
			this.current=tabNum-1;
		},
		changeTabBySwiper(e){
			this.tabNum=e.detail.current+1;
		},
		steadyChangeDate(date){
			this.steadyDate = date;
			this.steadyChooseDate(0);
			if(date=='申请'){
				return;
			}
			this.steadyPage=1;
			this.steadyList=[];
			this.querySteadyList();
		},
		transientChangeDate(date){
			this.transientDate = date;
			this.transientChooseDate(0);
			this.transientPage=1;
			this.transientList=[];
			this.queryTransientList();
		},
		querySteadyList(){
			var that = this;
			if(that.judgeLogin()){
				var reportType = '';
				if(that.steadyDate=='周报'){
					reportType=1
				}else if(that.steadyDate=='月报'){
					reportType=2
				}
				uni.request({
				    url: that.serverUrl+'/report/reportList', 
					method:'POST',
				    data: {
				       userId:uni.getStorageSync('userInfo').userId,
					   systemType:2,
					   reportType:reportType,
					   page:that.steadyPage++,
					   num:20
				    },
				    header: {
				        'content-type': 'application/x-www-form-urlencoded'
				    },
					sslVerify:false,
				    success: (res) => {
						uni.hideLoading();
						if(res.data.resultCode=='10000'){
							var list = that.addDateString(eval(res.data.data));
							if(list!=null&&list!=undefined&&list!=''&&list!=[]){
								that.steadyList = that.steadyList.concat(list);
							}
						}else{
							uni.showModal({
							    title: '提示',
							    content: '查询失败，请重试',
								confirmText:'重试',
							    success: function (res) {
							        if (res.confirm) {
							            that.querySteadyList();
							        } else if (res.cancel) {
							            console.log('用户点击取消');
							        }
							    },
							});
						}
				    },
					fail:(re)=>{
						uni.hideLoading();
						uni.showModal({
						    title: '提示',
						    content: '网络异常，请检查网络后重试',
							showCancel:false,
						    success: function (res) {
						        if (res.confirm) {
						            console.log('用户点击确定');
						        } else if (res.cancel) {
						            console.log('用户点击取消');
						        }
						    },
						});
					}
				});
			}	
		},
		queryTransientList(){
			var that = this;
			if(that.judgeLogin()){
					var reportType = '';
					if(that.transientDate=='周报'){
						reportType=1
					}else if(that.transientDate=='月报'){
						reportType=2
					}
					uni.request({
					    url: that.serverUrl+'/report/reportList', 
						method:'POST',
					    data: {
					       userId:uni.getStorageSync('userInfo').userId,
						   systemType:1,
						   reportType:reportType,
						   page:that.transientPage++,
						   num:20
					    },
					    header: {
					        'content-type': 'application/x-www-form-urlencoded'
					    },
						sslVerify:false,
					    success: (res) => {
							uni.hideLoading();
							if(res.data.resultCode=='10000'){
								var list = that.addDateString(eval(res.data.data));
								if(list!=null&&list!=undefined&&list!=''&&list!=[]){
									that.transientList = that.transientList.concat(list);
								}
							}else{
								uni.showModal({
								    title: '提示',
								    content: '查询失败，请重试',
									confirmText:'重试',
								    success: function (res) {
								        if (res.confirm) {
								            that.queryTransientList();
								        } else if (res.cancel) {
								            console.log('用户点击取消');
								        }
								    },
								});
							}
					    },
						fail:(re)=>{
							uni.hideLoading();
							uni.showModal({
							    title: '提示',
							    content: '网络异常，请检查网络后重试',
								showCancel:false,
							    success: function (res) {
							        if (res.confirm) {
							            console.log('用户点击确定');
							        } else if (res.cancel) {
							            console.log('用户点击取消');
							        }
							    },
							});
						}
					});
			}	
		},
		download(url){
			var that = this;
			uni.showLoading({
				title:'正在下载',
				mask:true
			})
			uni.downloadFile({
				url: url,
				success: (r) => {
					uni.hideLoading();
					if (r.statusCode === 200) {
						uni.saveFile({
							tempFilePath: r.tempFilePath,
							success: function(res) {
								 var savedFilePath = res.savedFilePath;
								uni.showModal({
									title: '下载报告成功',
									// content: '文档保存在：jxkj/CanNengApp/apps/'+savedFilePath,
									content: '下载成功，是否直接打开',
									success: function(re) {
										if (re.confirm) {
											 uni.openDocument({
											      filePath: r.tempFilePath
											    });
										} else if (res.cancel) {
											console.log('用户点击取消');
										}
									}
								});
							},
							fail() {
								uni.showModal({
									title: '提示',
									content: '下载报告失败，请重试',
									confirmText: '重试',
									success: function(res) {
										if (res.confirm) {
											that.download(url);
										} else if (res.cancel) {
											console.log('用户点击取消');
										}
									}
								})
							}
						});
					} else {
						uni.showModal({
							title: '提示',
							content: '下载报告失败，请重试',
							confirmText: '重试',
							success: function(res) {
								if (res.confirm) {
									that.download(url);
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						})
					}
				},
				fail() {
					uni.hideLoading();
					uni.showModal({
						title: '提示',
						content: '下载报告失败，请重试',
						confirmText: '重试',
						success: function(res) {
							if (res.confirm) {
								that.download(url);
							} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					})
				}
			});
		},
		applyReport(systemType){
			var that = this ;
			if(that.judgeLogin()){
				that.disabledFlag=true;
				that.loadingFlag=true;
				var lineIndex='';
				var quarter=0;
				var year=0;
				if(systemType==1){
					// if(that.transientLine==null||that.transientLine==undefined||that.transientLine.lineIndex==null||that.transientLine.lineIndex==undefined||that.transientLine.lineIndex==''){
					// 	uni.showModal({
					// 		title:"提示",
					// 		content:"请选择监测点",
					// 		showCancel:false
					// 	})
					// 	that.loadingFlag=false;
					// 	that.disabledFlag=false;
					// 	return;
					// }
					// lineIndex=that.transientLine.lineIndex;
					lineIndex=0;
				}else{
					if(that.steadyLine==null||that.steadyLine==undefined||that.steadyLine.lineIndex==null||that.steadyLine.lineIndex==undefined||that.steadyLine.lineIndex==''){
						uni.showModal({
							title:"提示",
							content:"请选择监测点",
							showCancel:false
						})
						that.loadingFlag=false;
						that.disabledFlag=false;
						return;
					}
					lineIndex=that.steadyLine.lineIndex;
				}
				that.loadingFlag=false;
				that.disabledFlag=false;
				uni.showLoading({
					mask:true
				})
				if(that.typesIndex==0){
					quarter=0;
					year=that.years[that.yearsIndex]
				}else{
					quarter=that.yearsAndQuarterIndex2+1;
					year=that.yearsAndQuarter[0][that.yearsAndQuarterIndex1]
				}
				uni.request({
				    url: that.serverUrl+'/report/customReport', 
					method:'POST',
				    data: {
				       userId:uni.getStorageSync('userInfo').userId,
					   systemType:systemType,
					   lineIndex:lineIndex,
					   quarter:quarter,
					   year:year
				    },
				    header: {
				        'content-type': 'application/x-www-form-urlencoded'
				    },
					sslVerify:false,
				    success: (res) => {
						uni.hideLoading()
						if(res.data.resultCode=='10000'){
							uni.showModal({
							    title: '提示',
							    content: '申请成功',
								showCancel:false,
							});
						}else{
							uni.showModal({
							    title: '提示',
							    content: '申请失败，请重试',
								confirmText:'重试',
							    success: function (res) {
							        if (res.confirm) {
							            that.applyReport(systemType);
							        } else if (res.cancel) {
							            console.log('用户点击取消');
							        }
							    },
							});
						}
				    },
					fail:(re)=>{
						uni.hideLoading()
						uni.showModal({
						    title: '提示',
						    content: '网络异常，请检查网络后重试',
							showCancel:false,
						    success: function (res) {
						        if (res.confirm) {
						            console.log('用户点击确定');
						        } else if (res.cancel) {
						            console.log('用户点击取消');
						        }
						    },
						});
					}
				});
			}	
		},
		steadyChooseDate(e){
			this.steadyDateChoose=e;
		},
		transientChooseDate(e){
			this.transientDateChoose=e;
		},
		addDateString(list){
			var that=this;
			for(var i=0;i<list.length;i++){
				list[i].dateString = that.formatTime(list[i].startTime)+'至'+that.formatTime(list[i].endTime)
			}
			return list;
		},
		changeType(e){
			this.typesIndex = e.detail.value;
		},
		changeYear(e){
			this.yearsIndex = e.detail.value;
		},
		changeYearsAndQuarter(e){
			 this.yearsAndQuarterIndex1 = e.detail.value[0];
			 this.yearsAndQuarterIndex2 = e.detail.value[1];
		},
		jumpToMyApply(systemType){
			uni.navigateTo({
				url:'../myApply/myApply?systemType='+systemType
			})
		},
		jumpToChooseLine(type){
			var that=this;
			uni.navigateTo({
				url:'../chooseLine/chooseLine'
			});
			uni.$on('lineInfoSelect',function(data){
				if(type==1){
					that.transientLine=data
				}else{
					that.steadyLine=data
				}
			});
			uni.$on('lineInfoQuery',function(data){
				if(type==1){
					that.transientLine=data
				}else{
					that.steadyLine=data
				}
			})
		}
	},
	onLoad() {
		// var userInfo = {};
		// userInfo.userId = '00c37ea1-b5ff-4f35-ba15-51e1c7363e20';
		// userInfo.roleCode=0;
		// uni.setStorageSync('userInfo',userInfo);
		var that = this;
		if(that.judgeLogin()){
			that.userInfo = uni.getStorageSync('userInfo');
			console.log(that.userInfo.userId)
			that.scrollH();
			that.querySteadyList();
			that.queryTransientList();
		}
	},
	onShow() {
		var that = this;
		if(that.judgeLogin()){
			var userInfo=uni.getStorageSync('userInfo');
			if(that.userInfo.roleCode!=userInfo.roleCode){
				that.userInfo=userInfo
			}
		}	
	}
}
